import {
    showUser,
    showIdentity,
    showApiAuthority,
    showApiIdentity,
    showViewAuthority,
    showViewIdentity
} from '../../service/userManger'
export default {
    namespace:'showUser',
    state:{
        user:[],
        identity:[],
        api_authority:[],
        identity_api_authority_relatioin:[],
        view_authority:[],
        identity_view_authority_relatioin:[],
        views:[]
    },
    reducers:{
        changeState(state,payload){
            return JSON.parse(JSON.stringify({...state,...payload}));
        }
    },
    effects:{
        *requestUser(payload,{put,call}){
            let result = yield call(showUser);
            if(result.code === 1){
                yield put({type:'changeState',user:result.data});
            }
        },
        *requestIdentity(payload,{put,call}){
            let result = yield call(showIdentity);
            if(result.code === 1){
                yield put({type:'changeState',identity:result.data});
            }
        },
        *requestApiAuthority(payload,{put,call}){
            let result = yield call(showApiAuthority);
            if(result.code === 1){
                yield put({type:'changeState',api_authority:result.data});
            }
        },
        *requestIdentityApi(payload,{put,call}){
            let result = yield call(showApiIdentity);
            if(result.code === 1){
                yield put({type:'changeState',identity_api_authority_relatioin:result.data});
            }
        },
        *requestViewAuthority(payload,{put,call}){
            let result = yield call(showViewAuthority);
            if(result.code === 1){
                yield put({type:'changeState',view_authority:result.data});
            }
        },
        *requestIdentityView(payload,{put,call}){
            let result = yield call(showViewIdentity);
            if(result.code === 1){
                yield put({type:'changeState',identity_view_authority_relatioin:result.data});
            }
        }
    }
}